import { __decorate } from "tslib";
import Vue from 'vue';
import Card from '@/components/Card.vue';
import { Component, Ref } from "vue-property-decorator";
import { photoSizes, getPhotoSize } from '@/model/PhotoSize';
import { Mutation } from 'vuex-class';
//300DPI时的换算系数
const DPICoefficient = 25.4;
let Index = class Index extends Vue {
    //轮播图
    banners = [
        'https://cdn.icuzz.com/photo/tiny-banner.png',
    ];
    //热门照片尺寸
    hostList = photoSizes.slice(0, 10);
    //自定义尺寸弹窗
    customSize = false;
    customSizeForm = {
        height: undefined,
        width: undefined,
        dpi: 300
    };
    sizeForm;
    //设置vuex
    setPhotoSize;
    //数字转换
    numberCase(index) {
        const num = index + 1;
        return num >= 10 ? num.toString() : '0' + num;
    }
    get mmWidth() {
        const { customSizeForm } = this;
        return Math.round(customSizeForm.width * DPICoefficient / customSizeForm.dpi);
    }
    get mmHeight() {
        const { customSizeForm } = this;
        return Math.round(customSizeForm.height * DPICoefficient / customSizeForm.dpi);
    }
    /**
     * 校验规则
     */
    rules = {
        height: [
            {
                required: true,
                message: "请输入高度",
                trigger: ['change', 'blur'],
            },
            {
                type: 'integer',
                message: "请输入整数",
                trigger: ['change', 'blur'],
            },
            {
                validator: (rule, value, callback) => {
                    if (Number(value) >= 80 && Number(value) <= 1200)
                        callback();
                    else
                        callback(new Error());
                },
                message: "高度应该在80-1200px之间",
                trigger: ['change', 'blur'],
            }
        ],
        width: [
            {
                required: true,
                message: "请输入宽度",
                trigger: ['change', 'blur'],
            },
            {
                type: 'integer',
                message: "请输入整数",
                trigger: ['change', 'blur'],
            },
            {
                validator: (rule, value, callback) => {
                    if (Number(value) >= 80 && Number(value) <= 1200)
                        callback();
                    else
                        callback(new Error());
                },
                message: "宽度应该在80-1200px之间",
                trigger: ['change', 'blur'],
            }
        ]
    };
    mounted() {
        this.sizeForm.setRules(this.rules);
    }
    /**
     *
     * @param pxSize 将
     */
    getMMSize(pxSize) {
    }
    openCustomSize(flag) {
        this.customSize = flag;
    }
    goToGenerate() {
        const { mmHeight, mmWidth, customSizeForm: { height, width, dpi }, setPhotoSize } = this;
        this.sizeForm.validate((valid) => {
            if (valid) {
            }
            else {
                uni.showToast({
                    title: "请按照规则填写宽度和高度"
                });
            }
        }).then(() => {
            const photoSize = {
                name: "自定义尺寸",
                pxHeight: height,
                pxWidth: width,
                mmHeight: mmHeight,
                mmWidth: mmWidth,
                dpi: dpi
            };
            uni.navigateTo({
                url: '/pages/photo/index',
                success() {
                    setPhotoSize(photoSize);
                }
            });
        }).catch((error) => {
            uni.showToast({
                title: error[0].message
            });
        });
    }
    /**
     * 跳转到图片选择页
     * @param id 图片大小配置的id
     */
    navigateToPhotoSelect(id) {
        const { setPhotoSize } = this;
        const photoSize = getPhotoSize(id);
        if (photoSize) {
            uni.navigateTo({
                url: '/pages/photo/index?id=' + id,
                success() {
                    setPhotoSize(photoSize);
                }
            });
        }
    }
};
__decorate([
    Ref('sizeForm')
], Index.prototype, "sizeForm", void 0);
__decorate([
    Mutation('SET_PHOTO_SIZE')
], Index.prototype, "setPhotoSize", void 0);
Index = __decorate([
    Component({ components: { Card } })
], Index);
export default Index;
